.processing__title{position:relative;background:#fff;padding-top:30px;padding-bottom:30px}.processing__title p{margin:0;position:absolute;top:50%;right:0;left:0;display:flex;align-items:center;justify-content:center;font-weight:500;text-align:center;color:#f61;transform:translateY(-50%)}@media(max-width: 767.98px){.processing__title p{padding-right:15px;justify-content:flex-end}}.processing__title p i{margin-right:10px}.processing__title h3{margin-bottom:0;position:relative;z-index:1}.processing__title h3 a{padding-left:50px;position:relative;display:inline-block;color:inherit}.processing__title h3 a i{position:absolute;top:50%;left:0;display:flex;align-items:center;justify-content:center;width:40px;height:40px;color:#828a96;border-radius:50%;background:#f6f7f9;transform:rotate(180deg) translateY(50%)}.processing__title h3 a:hover{text-decoration:none}.processing__title h3 span{margin-top:5px;display:block;font-size:14px;font-weight:normal}@media(min-width: 768px){.processing__title h3 span{font-size:16px}}.processing__holder{margin-bottom:50px;background:#f6f7f9;padding-top:25px;padding-bottom:25px}@media(min-width: 992px){.processing__holder{margin-bottom:100px;padding-top:50px;padding-bottom:50px}}.processing__holder .woocommerce{display:flex;flex-wrap:wrap;justify-content:space-between}